body {
    background-color: #1a222d;
    font-family: "Merriweather", serif;
    overflow: visible;
}

.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #1a222d;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.preloader-spinner {
    width: 50px;
    height: 50px;
    border: 5px solid rgba(255, 255, 255, 0.3);
    border-top-color: #45a29e;
    border-radius: 50%;
    animation: spin 0.3s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

#mouse-circle {
    position: fixed;
    width: 450px;
    height: 450px;
    background: radial-gradient(
        circle,
        rgba(25, 40, 50, 0.8) 0%,
        rgba(69, 162, 158, 0) 60%
    );
    border-radius: 50%;
    pointer-events: none;
    transform: translate(-50%, -50%);
    mix-blend-mode: lighten;
    z-index: 9999;
    animation: none;
}

section {
    overflow: visible;
}

html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    scroll-behavior: smooth; /* Enables smooth scrolling */
}

.vertical-text {
    position: fixed;
    right: 5%;
    top: 80%;
    transform: translateY(-50%);
    writing-mode: vertical-rl;
    text-orientation: mixed;
    font-size: 15px;
    color: #dbdbdb;
    letter-spacing: 3px;
    text-align: center;
    z-index: 1000;
    display: flex;
    align-items: center;
}

.vertical-text::after {
    content: "";
    display: block;
    width: 1px;
    height: 150px;
    background-color: #dbdbdb;
    margin-left: 0px;
    margin-top: 30px;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: -10px;
    overflow: visible;
}

button {
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    font-family: "Merriweather", serif;
}

button:active {
    transform: scale(0.95);
}

.menu-icon {
    display: none;
}
#education {
    scroll-margin-top: 130px;
}
#about {
    scroll-margin-top: 40px;
}
#projects {
    scroll-margin-top: 40px;
}
